<template>
  <view class="main-container">
    <CardType
      :bg="'https://cdn-static.papa.com.cn/social/home-bg2.png'"
      :titleInfo="{ ...titleInfo, num: count }"
      @clickMore="handleClickMore"
    >
      <template #list>
        <view class="list-container">
          <view
            class="item"
            v-for="item in list"
            :key="item.stadium_id"
            @click="handleClickItem(item)"
          >
            <view class="top">
              <view class="item-img">
                <image class="img" :src="item.images_url[0]" alt="" mode="aspectFill" />
              </view>
              <view class="distance">{{ item.distance || '' }}</view>
            </view>
            <view class="item-title">{{ item.name }}</view>
          </view>
          <view v-if="list.length == 0" class="empty-container">
            <empty :marginTop="0"></empty>
          </view>
        </view>
      </template>
    </CardType>
  </view>
</template>

<script>
import { getStadiumList } from '@/apis/home/index'
import CardType from './CardType.vue'
export default {
  components: {
    CardType,
  },
  data() {
    return {
      list: [],
      count: 0,
      titleInfo: {
        title: '体育场馆',
        num: this.count,
        unit: '个',
        color: '#3576DF',
      },
    }
  },
  created() {
    this.getSportsStadiumList()
  },
  onLoad(options) {},
  methods: {
    handleClickMore() {
      uni.navigateTo({
        url: '/pagesSub/platform/stadium/index',
      })
    },
    async getSportsStadiumList() {
      let res = await getStadiumList({ page: 1, size: 5 })
      if (res.code == 200) {
        this.list = res.data.list
        this.count = res.data.count
      } else {
        this.$toast(res.message)
      }
    },
    handleClickItem(item) {
      uni.navigateTo({
        url: `/pagesSub/platform/stadium/detail?stadium_id=${item.stadium_id}`,
      })
    },
  },
}
</script>

